<template>
  <div class="indications">
    <app-table
      title="Indicações"
      :currentPage="currentPage"
      :headers="headers"
      :items="itemsModel"
      :loading="loading"
      :qtdPages="qtdPages"
      :statusItems="statusItems"
      @onPageChanged="changePage"
      @onActionClicked="callAction"
    />
  </div>
</template>

<script>
import AppTable from '@/components/moderators/Table';

import DateMixin from '@/mixins/DateMixin';
import SnackbarMixin from '@/mixins/SnackbarMixin';
import TextMixin from '@/mixins/TextMixin';

export default {
  components: { AppTable },
  mixins: [ DateMixin, SnackbarMixin, TextMixin ],
  data: () => ({
    currentPage: null,
    headers: [
      { text: '#', value: 'id', align: 'center', width: '60' },
      { text: 'Contactado', value: 'actions' },
      { text: 'Data', value: 'created_at' },
      { text: 'Estabelecimento', value: 'barbershop' },
      { text: 'Responsável', value: 'responsable' },
      { text: 'Telefone', value: 'phone' },
    ],
    items: [],
    loading: true,
    order: null,
    qtdPages: 1,
    search: null,
    status: null,
    statusItems: [
      { text: 'Todas', value: null },
      { text: 'Aguardando aprovação', value: 1 },
      { text: 'Ativas', value: 2 },
      { text: 'Bloqueadas', value: 3 }
    ]
  }),
  computed: {
    itemsModel () {
      return this.items.map((item) => {
        const date = new Date(item.created_at);
        const actions = [
          {
            class:  item.contacted ? 'success' : 'gray1',
            action:  item.contacted ? '' : 'contact',
            text: 'Contactado'
          }
        ];

        return {
          id: item.id,
          actions: actions,
          barbershop: item.barbershop,
          responsable: item.responsable,
          phone: this.setPhoneMask(item.phone),
          created_at: this.formatDateTimeToStringPtBr(date)
        };
      });
    }
  },
  mounted () {
    this.getIndications();
  },
  methods: {
    getIndications () {
      let url = `${this.$url_api}/indication?paginate=true&orderBy=created_at&order=desc&`
      if (this.currentPage) url += `page=${this.currentPage}&`;
      
      this.$axios(url)
      .then(response => {
        if (response.status === 200) {
          if (response.data.codigo === 200) {
            const responseData = response.data.corpo;
            this.items = responseData.data;
            this.qtdPages = responseData.last_page;
            this.currentPage = responseData.current_page
          }
        }
      }).finally(() => this.loading = false);
    },
    changePage(page) {
      this.currentPage = page;
      this.getIndications();
    },
    callAction(action, item) {
      if (action == 'contact') {
        this.makeContact(item.id)
      }
    },
    makeContact (indicationId) {
      this.$axios.patch(`${this.$url_api}/indication/${indicationId}/make-contact`)
      .then(response => {
        if (response.status === 200) {
          if (response.data.codigo === 200) {
            if (response.data.corpo) {
              const index = this.itemsModel.findIndex((item) => item.id == indicationId);
              if (index > -1) {
                this.items[index].contacted = true;
              }
              this.showSnackbar('Indicação atualizada')
            } else this.setError()
          } else this.setError()
        } else this.setError()
      }).catch(() => this.setError());
    },
    setError() {
      this.showSnackbarErro('Não foi possível atualizar a indicação!');
    },
  }
}
</script>